<template>
	<div class="cashLog-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<template v-if="loadingSpinner">
			<van-loading type="spinner" size="24px" class="ui-center">加载中...</van-loading>
		</template>
		<template v-else>
			<div class="list" v-if="list.length">
				<div class="list-item mb10" v-for="item in list" :key="item.id">
					<div class="list-item-hd">
						<div class="list-item-title">{{item.typeStr}}</div>
						<div class="list-item-txt">{{item.dateAdd}}</div>
					</div>
					<div class="list-item-bd" :class="item.behavior ? 'ui-c-red': 'ui-c-green'">{{item.behavior? '-' : '+' }}{{item.amount}}</div>
				</div>
			</div>
			<div class="no-data" v-else>
				<van-loading text-size="16" class="ui-center">暂无数据</van-loading>
			</div>
		</template>
	</div>
</template>

<script>
	import util from '../../common/util/util.js'
	import topNav from '../../components/topNav.vue'
	export default {
		data() {
			return {
				list: [],
				loadingSpinner: true,
				title:'',
				isleftarrow:''
			}
		},
		components: {
			topNav
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		created() {
			this.getCashLog();
		},
		methods: {
			getCashLog(page = 1, pageSize = 50) {
				this.loadingSpinner = true;
				this.$request.post('/user/cashLog/v2', {
					token: util.storage.get('token')
				}).then(res => {
					this.loadingSpinner = false;
					console.log(res);
					if (res.code !== 0) {
						return;
					}
					this.list = res.data.result;
					console.log(this.list)
				})
			},
			toSetting() {

			}
		}
	}
</script>

<style lang="less" scoped>
	.no-data .van-loading__spinner {
		display: none;
	}

	.list {
		padding: 15px;
	}

	.list-item {
		overflow: hidden;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 20px 15px;
		background: #fff;

		&-hd {
			// flex: 1;
			// text-align: start;
			// font-size: 20px;
		}

		&-bd {
			// flex:1;
			font-size: 20px;
		}

		&-title {
			padding: 0 15px;
			font-size: 16px;
			color: #333;
			margin-bottom: 5px;
		}

		&-txt {
			font-size: 14px;
			color: #999;
		}

		&-actions {
			padding: 0 15px 5px;
			font-size: 12px;
			text-align: right;
		}
	}
</style>
